import React, { Component } from 'react';
import { connect } from 'react-redux'; 
import * as actionCreators from "../../store/actions/index";
import Button from "../../components/UI/Button/Button";
import Modal from "../../components/UI/Modal/Modal";
import Spinner from "../../components/UI/Spinner/Spinner";
import { voiceOver } from '../../utilities/utilities'
import './SentencesConstructor.css';

class SentencesConstructor extends Component {
	state = {
		currentSet: 0,
		currentSentence: '',
		currentSentenceIndex: 0,
		totalSentencesQty: 10,
		sentence: [],
		correctSentence: [],
		taskName: '',
		answeredSentence: [],
		totalCorrect: 0,
		isIncorrect: false,
	};

	answeredRef = React.createRef();

	componentDidMount() {
		this.props.fetchData(
			`https://spreadsheets.google.com/feeds/list/1n5633PymXXc-qW_UWMMFxaJYpR-r5PoXrGMaOn7VfF0/${this.props.page}/public/values?alt=json`
		);
	}

	componentDidUpdate(prevProps, prevState) {
		const isTaskCompleted = this.state.currentSentenceIndex >= this.state.totalSentencesQty;
		const newSetShouldBeApplied = this.state.totalCorrect && prevState.totalCorrect !== this.state.totalCorrect;
		if (prevProps.data === this.props.data && !newSetShouldBeApplied || isTaskCompleted) return;
		const setPath = this.props.data[this.state.currentSentenceIndex];

		this.setState({
			taskName: setPath.taskName,
			sentence: setPath.wordSets,
			currentSentence: setPath.translation,
			correctSentence: setPath.correctSentence,
			answeredSentence: [],
			totalSentencesQty: this.props.data.length,
		});
	}

	isSentenceCorrect = () => {
		const {
			correctSentence,
			answeredSentence,
		} = this.state;

		return correctSentence.join(' ') === answeredSentence.join(' ');
	};

	highlightErrors = () => {
		const {
			correctSentence,
			answeredSentence,
		} = this.state;
		
		const answeredWords = this.answeredRef.current.children;

		correctSentence.forEach((word, ind) => {
			(word !== answeredSentence[ind]) && answeredWords[ind + 1].classList.add('-incorrect');
		});

		this.setState({ isIncorrect: true });
	};

	selectWord = (e) => {
		const word = e.target.textContent;
		const checkCorrectness = () => {
			const {
				correctSentence,
				answeredSentence,
			} = this.state;
			const allWordsAreSelected = correctSentence.length === answeredSentence.length;

			if (allWordsAreSelected) {
				const sentenceIsCorrect = this.isSentenceCorrect();

				if (sentenceIsCorrect) {
					this.setState((prevState) => ({
						totalCorrect: prevState.totalCorrect + 1,
						currentSentenceIndex: prevState.currentSentenceIndex + 1,
						currentSet: 0,
					}));
				} else {
					this.highlightErrors();
				}
			}
		};

		this.setState((prevState) => ({
			answeredSentence: prevState.answeredSentence.concat(word),
			currentSet: prevState.currentSet + 1,
		}), checkCorrectness);
	};

	removeLastWord = () => {
		this.setState((prevState) => ({
			answeredSentence: prevState.answeredSentence.slice(0, -1),
			currentSet: prevState.currentSet - 1,
		}));
	};

	render() {
		if (this.props.loading) {
	      return <Spinner />;
	    } else if (this.state.totalCorrect === this.state.totalSentencesQty) {
	    	return <Modal>Wiktory!</Modal>
	    }

		return (
			<div className="wrapper">
				<div className="task-name">{this.state.taskName}</div>
				<div className="current-sentence">{this.state.currentSentence}</div>
				<div
					ref={this.answeredRef}
					className="answer"
				>
					<span
						className={`voice-over-icon${this.state.isIncorrect ? '' : ' -hidden'}`}
						onClick={() => voiceOver(this.state.correctSentence.join(','))}
					/>
					{
						this.state.answeredSentence.map((el, ind) => (
							[<span key={el + ind}>{el}</span>, ' ']
						))
					}
					<i className={`delete-word${!this.state.answeredSentence.length ? ' -hidden' : ''}`} onClick={this.removeLastWord} />
				</div>
				<ul className="sentence-tiles">
					{this.state.sentence.length && this.state.currentSet !== this.state.sentence.length && this.state.sentence[this.state.currentSet].map((word) => (
						<li
							key={word}
							onClick={this.selectWord}
						>{word}</li>
					))}
				</ul>
				<button className="show-rule">Show the Rule</button>
				<div className="progress-bar-holder">
					<ol className="progress-bar">
						{Array(this.state.totalSentencesQty).fill(1).map((sentence, index) => (
							<li className={`progress-dot${this.state.currentSentenceIndex === index ? ' -current' : ''}`} key={index} />
						))}
					</ol>
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		data: state.wordsConstructor.fetchedData,
		loading: state.wordsConstructor.loading,
		error: state.wordsConstructor.error
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
	  fetchData: url => dispatch(actionCreators.asyncFetchStart(url, { taskType: 'sentences' }))
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(SentencesConstructor);


